<template>
  <div class="header">
    <div class="container clearfix">
      <div class="header-left pull-left">
        <p class="header-left-logo"></p>
        <ul class="header-left-menu clearfix">
          <li :class="{'is-active': active === '#home'}" @click="handleScrollClick('#home')">首页</li>
          <li :class="{'is-active': active === '#intro'}" @click="handleScrollClick('#intro')">简介</li>
          <li :class="{'is-active': active === '#tech'}" @click="handleScrollClick('#tech')">技术介绍</li>
          <li :class="{'is-active': active === '#team'}" @click="handleScrollClick('#team')">团队介绍</li>
          <li :class="{'is-active': active === '#invest'}" @click="handleScrollClick('#invest')">投资顾问</li>
          <li :class="{'is-active': active === '#media'}" @click="handleScrollClick('#media')">媒体报道</li>
          <li :class="{'is-active': active === '#link'}" @click="handleScrollClick('#link')">联系我们</li>
        </ul>
      </div>
      <div class="header-right pull-right">
        <p class="header-right-lan">中文<i class="iconfont icon-pulldown"></i></p>
        <a class="header-right-down" href="/static/nova.pdf" download="nova"><i class="iconfont icon-down"></i>白皮书</a>
      </div>
    </div>
  </div>
</template>
<style lang="less" scoped>
  .header {
    position: fixed;
    z-index: 1000;
    width: 100%;
    height: 64px;
    font-size: 14px;
    background-color: #ffffff;
    &-left {
      margin-left: 44px;
      height: 100%;
      &-logo {
        width: 103px;
        height: 64px;
        display: inline-block;
        background: url("../../assets/img/header_logo.png") no-repeat left 15px;
        background-size: 100% 34px;
      }
      &-menu {
        display: inline-block;
        margin-left: 70px;
        height: 64px;
        color: #999999;
        li {
          float: left;
          line-height: 64px;
          cursor: pointer;
          & + li {
            margin-left: 40px;
          }
          &.is-active {
            color: #F5A623;
          }
          &:hover {
            color: #F5A623;
          }
        }
      }
    }
    .header-right {
      margin-right: 20px;
      &-lan {
        line-height: 64px;
        display: inline-block;
        color: #9C9C9C;
        cursor: pointer;
        i {
          margin-left: 7px;
          font-size: 6px;
          vertical-align: middle;
        }
      }
      &-down {
        display: inline-block;
        padding: 0 11px;
        box-sizing: border-box;
        height: 36px;
        line-height: 36px;
        margin: 14px 40px 0;
        border-radius: 2px;
        font-size: 14px;
        color: #ffffff;
        background-color: #F5A623;
        cursor: pointer;
        i {
          font-size: 17px;
          margin-right: 3px;
          vertical-align: middle;
        }
        &:hover {
          opacity: 0.9;
        }
      }
    }
  }
</style>
<script>
  export default {
    data() {
      return {
        active: '#home'
      }
    },
    methods: {
      //  点击菜单
      handleScrollClick(selector) {
        this.active = selector
        this.$emit('headerClick', selector)
      }
    }
  }
</script>



